<template>
    <yd-layout>
        <ul>
            <li v-for="(item, index) in updataApp" @click="accessRecord('重磅更新-'+ item.CHNLNAME);jump(item)" :key="index">
                <div class="item">
                    <div class="imgBox">
                        <img class="firImg" :src="'https://mobileoa.unionpay.com/portalMgmt2/attachments/'+item.IMG" />
                        <!-- <div class="new edit" v-if="item.ISRECOMM == 3">
                            <img src="../../../static/images/app/appNew.png" alt="">
                        </div> -->
                    </div>
                </div>
                <div class="item center">
                    <p class="title">{{item.CHNLNAME}}
                        <span v-if="item.ISRECOMM == 1" class="iconNew">new</span>
                    </p>
                    <div class="des">
                        <p :class="item.more == '更多' ? '' : 'all'" v-text="'描述:'+item.DESCRIPTION"></p>
                        <span v-text="item.more" @click="changMore(item.CHNLNAME, item.more, $event)"></span>
                    </div>
                </div>
                <!-- <div class="item goApp">
                    <div @click="jump(item)">进入应用</div>
                </div> -->
            </li>
        </ul>
        <div class="bottom_line">
            <div></div>
            <p>我是可爱的底线</p>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
import axios from 'axios';
export default {
    name: 'appList',
    data() {
        return {
            updataApp: [],
            seeMore: false
        }
    },
    mounted: function () {
        // 8月16号上线
        document.setTitle('重磅更新');
        this.pageRender();
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        pageRender() {
            const vm = this;
            vm.get('/portalMgmt/oa/menus/getAllApplications', {}, function (res) {
                Object.values(res.jsonData[0]).forEach(function (data) {
                    vm.updataApp.push(...data);
                });
                // 判断是添加还是删除的图标
                vm.updataApp = vm.updataApp.filter((item) => {
                    return item.URL.indexOf('http') == -1;
                });
                vm.updataApp.sort(function (a, b) {
                    return b.ISRECOMM - a.ISRECOMM;
                });
                vm.updataApp.forEach(function (item) {
                    item.more = '更多';
                });
            });
        },
        accessRecord(name) {
            let vm = this;
            axios.get(vm.conf.portalMobile + '/portal/report/accessRecord', {
                params: {
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'moduleName': name
                }
            }).then(function (data4) {});
        },
        jump(data) {
            const vm = this;
            if (vm.HOST != 'http://172.18.44.44' && vm.HOST != 'http://localhost:8080') {
                if (data.CHNLNAME == '费控宝') {
                    let dTime = new Date().getTime();
                    let feiUrl = `http://poc.feikongbao.net:8080/h5/#/ssoLogin?loginid=${vm.userInfo.userid}&stamp=${dTime}&token=${vm.SHA1('U2FsdGVkX195zejeVG15PoO7Nlf9q4D34rt0okijn' + vm.userInfo.userid + dTime)}`;
                    UPCHAT.M.NAPI.startNewWebView(feiUrl, { 'sysid': data.SYSID });
                } else {
                    let nUrl;
                    if (data.URL.indexOf('http') != -1) {
                        if (data.URL.indexOf('{username}') != -1) {
                            nUrl = data.URL.replace(/{username}/, vm.userInfo.userid);
                        } else if (data.URL.indexOf('{empcode}') != -1) {
                            nUrl = data.URL.replace(/{empcode}/, vm.userInfo.empcode);
                        } else {
                            nUrl = data.URL
                        }
                    } else {
                        nUrl = this.HOST + data.URL;
                    }
                    UPCHAT.M.NAPI.startNewWebView(nUrl, { 'sysid': data.SYSID });
                }
            } else {
                if (data.URL.indexOf('{username}') != -1) {
                    data.URL = data.URL.replace(/{username}/, vm.userInfo.userid);
                    window.location.href = data.URL;
                } else if (data.URL.indexOf('{empcode}') != -1) {
                    data.URL = data.URL.replace(/{empcode}/, vm.userInfo.empcode);
                    window.location.href = data.URL;
                } else {
                    window.location.href = data.URL;
                }
            }
        },
        changMore(title, name, ev) {
            const vm = this;
            this.updataApp.forEach((item, index) => {
                if (item.CHNLNAME == title) {
                    let obj = item;
                    obj.more = name == '更多' ? '收起' : '更多';
                    vm.$set(vm.updataApp, index, obj);
                }
            });
            // 如果提供了事件对象，则这是一个非IE浏览器
            if (ev && ev.stopPropagation) {
                ev.stopPropagation();
            } else {
                window.event.cancelBubble = true;
                return false;
            }
        }
    }
}
</script>
<style lang="less">
.yd-scrollview {
    height: 100%;
    background: #fff;
}

li {
    border-bottom: 1px solid #eaeaea;
    background: #fff;
    overflow: hidden;
    &:last-child {
        border-bottom: none;
    }
    .item {
        float: left;
        padding-top: 0.3rem;
        padding-bottom: 0.25rem;
        &:nth-child(1) {
            width: 1.6rem;
        }
        &:nth-child(2) {
            width: 5.7rem;
        }
        &:nth-child(3) {
            width: 1.3rem;
        }
    }
}

.imgBox {
    height: 1rem;
    width: 1.18rem;
    position: relative;
    margin-left: 0.2rem;
    text-align: center;
    margin-top: .2rem;
    img {
        height: .8rem;
        width: .8rem;
    }
    .edit {
        display: block;
        width: 0.54rem;
        height: 0.42rem;
        position: absolute;
        top: -0.16rem;
        right: -0.14rem;
        img {
            width: 100%;
            height: 100%;
        }
    }
}

.center {
    .title {
        color: #333;
        font-size: 0.28rem;
        font-weight: 600;
        height: 0.74rem;
        .iconNew {
            display: inline-block;
            color: #fff;
            background: #ff4e55;
            border-radius: 3px;
            margin-left: 0.18rem;
            height: 0.3rem;
            position: relative;
            top: -0.04rem;
            line-height: 0.28rem;
            font-weight: normal;
            font-size: 10px;
            padding: 0 0.08rem;
        }
    }
    .des {
        color: #999;
        font-size: 0.24rem;
        span {
            float: left;
            color: #59a5fd;
            line-height: 0.3rem;
        }
        p {
            float: left;
            max-width: 4.9rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            margin-right: 0.08rem;
            line-height: 0.3rem;
        }
        p.all {
            white-space: inherit;
        }
    }
}

.goApp {
    padding-top: 0.5rem !important;
    div {
        width: 1.3rem;
        height: 0.5rem;
        line-height: 0.5rem;
        background: #59a5fd;
        color: #fff;
        font-size: 0.24rem;
        text-align: center;
        border-radius: 0.08rem;
    }
}

.bottom_line {
    color: #999;
    font-size: 0.24rem;
    height: 0.8rem;
    background: #fff;
    position: relative; // position: fixed;
    // bottom: -1px;
    width: 100%;
    p {
        width: 2rem;
        height: 0.4rem;
        line-height: 0.4rem;
        background: #fff;
        position: relative;
        display: block;
        z-index: 9;
        text-align: center;
        margin: 0 auto;
    }
    div {
        position: absolute;
        top: 0.2rem;
        z-index: 9;
        width: 80%;
        left: 10%;
        border-bottom: 1px solid #eaeaea;
    }
}
</style>


